#page-container {
  position: fixed;
  top: 64px;
  left: 0;
  right: 0;
  bottom: 0;

  overflow-x: hidden;
  overflow-y: auto;
}

#content-container {
  overflow: hidden;

  padding: 40px;
  width: calc(100% - 80px);
}


/* Media queries */

@media (max-width: 1000px) {
  #content-container {
    padding: 30px;
    width: calc(100% - 60px);
  }
}

@media (max-width: 599px) {
  #page-container {
    top: 48px;
  }
}

@media (max-width: 480px) {
  #page-container {
    top: 104px;
  }

  #content-container {
    padding: 15px;
    width: calc(100% - 30px);
  }
}

@media (max-width: 480px) and (orientation: landscape) {
  #page-container {
    top: 96px;
  }
}



